/*----------------changes for setka editor posts--------------------------------------*/
/*------------------------------------------------------------------------------------*/
.blog-post-content .stk-post.stk-post.stk-post {
  margin-left: -70px;
  margin-right: -70px;
  padding-right: 70px;
  padding-left: 70px;
  box-sizing: border-box;
  overflow-x: initial;
}
.blog-post-content .stk-label {
  height: 0;
  overflow: hidden;
}
.blog-post-content .stk-post.stk-post.stk-post p,
.blog-post-content .stk-post.stk-post.stk-post p strong,
.blog-post-content .stk-post.stk-post.stk-post p.stk-reset b,
.blog-post-content .stk-post.stk-post.stk-post p.stk-reset strong {
  text-transform: none;
  font-family: "Roboto", sans-serif;
  color: #565656;
  font-size: 18px;
  line-height: 1.5;
}
.blog-post-content .stk-post.stk-post.stk-post a:link,
.blog-post-content .stk-post.stk-post.stk-post a:visited,
.blog-post-content .stk-post.stk-post.stk-post a.stk-reset:visited,
.blog-post-content .stk-post.stk-post.stk-post a,
.blog-post-content .stk-post.stk-post.stk-post a.stk-reset {
  text-transform: none;
  text-decoration: none;
  font-size: inherit;
  color: inherit;
  border-bottom: 1px solid;
  border-color: inherit;
}
.blog-post-content .stk-post.stk-post.stk-post  .stk-grid:not(.equal-rows-grid) .stk-grid-col.stk-grid-col_last[data-col-width="2"] {
  margin-right: -70px;
  /*width: calc(((100% - 100px) / 6) * 2 + 90px);*/
  max-width: 370px;
}
.blog-post-content .stk-post.stk-post.stk-post p u {
  color: #212121;
  text-decoration: none;
}
.blog-post-content .stk-post.stk-post.stk-post .stk-grid-col.stk-grid-col_last u {
  font-family: "Roboto", sans-serif;
  color: #212121;
  font-size: 26px;
  font-weight: 400;
  line-height: 30px;
  display: block;
  padding-top: 7px;
  text-align: right;
}
.blog-post-content .stk-post.stk-post.stk-post .stk-grid-col + .stk-grid-col.stk-grid-col_last p + p a.stk-reset{
  border-bottom: 1px solid;
  border-color: inherit;
  color: #a3a3a3;
}
.blog-post-content .stk-post.stk-post.stk-post .stk-grid-col + .stk-grid-col.stk-grid-col_last p + p a.stk-reset:hover,
.blog-post-content .stk-post.stk-post.stk-post .stk-grid-col + .stk-grid-col.stk-grid-col_last p + p a.stk-reset:active {
  color: #f1563c;
}
.blog-post-content .stk-post.stk-post.stk-post .stk-grid-col.stk-grid-col_last u:before {
  display: block;
  content: '';
  margin: 0 auto 30px;
  border-right: 72px solid #f1563c;
  height: 3px;
}
.blog-post-content .stk-post.stk-post.stk-post .stk-grid-col.stk-grid-col_last u a,
.blog-post-content .stk-post.stk-post.stk-post .stk-grid-col.stk-grid-col_last u a:link,
.blog-post-content .stk-post.stk-post.stk-post .stk-grid-col.stk-grid-col_last u a:visited {
  color: #a3a3a3;
  font-size: 16px;
  font-weight: 400;
  line-height: inherit;
  border-bottom: none;
  display: block;
}
.blog-post-content .stk-post.stk-post.stk-post .stk-grid-col.stk-grid-col_last u a:hover {
  color: #f1563c;
}
.blog-post-content .stk-post.stk-post.stk-post p.stk-element_no-text,
.blog-post-content .stk-post.stk-post.stk-post .stk-grid-col[data-col-width="6"] .stk-image-figure,
