  margin-right: -70px;
  width: auto;
  max-width: none;
}
.blog-post-content .stk-post.stk-post.stk-post p.stk-element_no-text a {
  display: inline-block;
  border-bottom: none;
}
.blog-post-content .stk-post.stk-post.stk-post .stk-grid-col[data-col-width="1"] .stk-image-figure,
.blog-post-content .stk-post.stk-post.stk-post .stk-grid-col[data-col-width="2"] .stk-image-figure,
