.blog-post-content .stk-post.stk-post.stk-post .stk-grid-col[data-col-width="4"] .stk-image-figure,
.blog-post-content .stk-post.stk-post.stk-post .stk-grid-col[data-col-width="5"] .stk-image-figure,
