  max-width: 100%;
  margin-left: 0;
  margin-right: 0;
}
.blog-post-content .stk-post.stk-post.stk-post figcaption a:link,
.blog-post-content .stk-post.stk-post.stk-post figcaption a:visited,
.blog-post-content .stk-post.stk-post.stk-post figcaption a {
  font-size: inherit;
  color: inherit;
}
.blog-post-content .stk-post.stk-post.stk-post p.stk-theme_8428__style_small_text,
.blog-post-content .stk-post.stk-post.stk-post p.stk-theme_10213__style_small_text{
  color: #595959;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  min-height: 19px;
}
.blog-post-content .stk-post.stk-post.stk-post > .stk-image-figure,
.blog-post-content .stk-post.stk-post.stk-post > .stk-embed-figure,
.blog-post-content .stk-post.stk-post.stk-post > .stk-gallery,
.blog-post-content .stk-post.stk-post.stk-post > hr {
  width: auto;
  max-width: 980px;
  margin-left: -70px;
  margin-right: -70px;
  box-sizing: border-box;
  text-align: center;
}
.blog-post-content .stk-post.stk-post.stk-post .stk-image-figure > .stk-mask > a {
    border-bottom: 0 !important;
}
.blog-post-content .stk-post.stk-post .stk-mask {
  display: block;
  position: relative;
}
