/*paragraph with list markup*/
.blog-post-content .stk-post.stk-post.stk-post p.list-item {
  padding: 0 74px;
  list-style: none;
  position: relative;
  line-height: 1.5;
  margin: 0 0 20px 0;
}
.blog-post-content .stk-post.stk-post.stk-post p.list-item + figure {
  margin-top: 37px;
}
.blog-post-content .stk-post.stk-post.stk-post p.list-item:before {
  display: block;
  position: absolute;
  left: 37px;
  content: '' !important;
  top: .5em;
  width: 9px;
  border-style: solid none none none;
  border-width: 9px;
  border-color: #f1563c;
}
.blog-post-content .stk-post.stk-post.stk-post .list-item.pros-list,
.blog-post-content .stk-post.stk-post.stk-post .list-item.cons-list {
  padding: 20px 38px 0 74px;
  margin: 0 0 30px;
  border-top: 1px solid #ccc;
}
.blog-post-content .stk-post.stk-post.stk-post .list-item.pros-list + .list-item.pros-list,
.blog-post-content .stk-post.stk-post.stk-post .list-item.cons-list + .list-item.cons-list{
  border-top: none;
  margin-top: -30px;
}
.blog-post-content .stk-post.stk-post.stk-post .list-item.pros-list:before,
.blog-post-content .stk-post.stk-post.stk-post .list-item.cons-list:before {
  top: 1.4em;
  width: 1px;
  border-style: none solid solid;
  border-width: 0 8px 14px;
  border-color: #78de6a transparent;
}
.blog-post-content .stk-post.stk-post.stk-post .list-item.cons-list:before {
  border-style: solid solid none;
  border-width: 14px 8px 0;
  border-color: #f0dd3f transparent;
}
/*video*/

.video-container,
.stk-embed-figure .stk-code {
  position: relative;
  padding-top: 56.25%;
  padding-bottom: 0;
  margin-bottom: 20px;
  height: 0;
  overflow: hidden;
}
.stk-embed-figure .stk-code iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
